.checkout-accept {
  &-title {
    @apply pb-5;
    p {
      @apply text-center text-4xl font-bold;
    }
  }
  &-content {
    @apply flex justify-between;
    .checkout-accept-list {
      width: calc(70% - 20px);
    }
    .checkout-accept-sidebar {
      width: calc(30% - 20px);
    }
  }
}